import {
  GoogleMap,
  DistanceMatrixService,
  useJsApiLoader,
} from "@react-google-maps/api";
import { useEffect, useState } from "react";
import React from "react";
import axios from "axios";

const Distance = (props) => {
  const key = process.env.REACT_APP_API_KEY;

  const { isLoaded } = useJsApiLoader({
    id: "google-map-script",
    googleMapsApiKey: key,
  });

  let destinations = [
    {
      name: "1",
      longitude: -38.4796142578125,
      latitude: -3.794373142650976,
      rate: 4.4,
      address:
        "Av. Washington Soares, 4335 - Lagoa Sapiranga (Coit), Fortaleza - CE, 60833-005, Brazil",
      placeId: "ChIJGYpFQ3tFxwcRaqpoPAH_xM4",
    },
    {
      name: "2",
      longitude: -38.61642837524414,
      latitude: -3.759430279719816,
      rate: 4.3,
      address:
        "Av. Dom Almeida Lustosa, 1780 - Parque Albano, Caucaia - CE, 61650-000, Brazil",
      placeId: "ChIJXXcRJ3xLxwcRk2a34wnYeE8",
    },
    {
      name: "3",
      longitude: -38.57402801513672,
      latitude: -3.803279921402868,
      rate: 4.4,
      address:
        "Av. Godofredo Maciel, 3190 - Maraponga, Fortaleza - CE, 60710-001, Brazil",
      placeId: "ChIJSZPS_dZNxwcR2MXNC-cZd7Y",
    },
  ];
  destinations = destinations.map((dest) => "place_id:" + dest.placeId);

  const findDistances = async () => {
    console.log(destinations);

    destinations.map(async (destination) => {
      const newDestinations = destinations.filter(
        (dest) => dest !== destination
      );
      console.log(newDestinations);
      const url = new URL(
        "https://maps.googleapis.com/maps/api/distancematrix/json?"
      );
      url.searchParams.append("origins", destination);
      const param = newDestinations.join("|");
      url.searchParams.append("destinations", param);
      url.searchParams.append("key", key);
      const res = await axios.get(url.href);
      console.log(url.href, "\n", res.data.rows[0].elements);
    });
  };
  findDistances();
  return <h1>Distance</h1>;
};

export default Distance;
